import Vue from "vue";

import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
	{
		path: "/",
		name: "home",
		component: () => import(/* webpackChunkName: "home" */ "../views/Home.vue"),
	},
	{
		path: "/create",
		name: "create",
		component: () => import(/* webpackChunkName: "create" */ "../views/Create.vue"),
	},
	{
		path: "/signup",
		name: "signup",
		component: () => import(/* webpackChunkName: "signup" */ "../views/auth/provider/Signup.vue"),
	},
	{
		path: "/login",
		name: "login",
		component: () => import(/* webpackChunkName: "login" */ "../views/auth/provider/Login.vue"),
	},
	{
		path: "/signup/local",
		name: "localSignup",
		component: () => import(/* webpackChunkName: "localSignup" */ "../views/auth/local/Signup.vue"),
	},
	{
		path: "/login/local",
		name: "localLogin",
		component: () => import(/* webpackChunkName: "localLogin" */ "../views/auth/local/Login.vue"),
	},
	{
		path: "/forget",
		name: "forgetPassword",
		component: () => import(/* webpackChunkName: "forgetPassword" */ "../views/auth/local/ForgetPassword.vue"),
	},
	{
		path: "/events",
		name: "events",
		component: () => import(/* webpackChunkName: "events" */ "../views/Events.vue"),
	},
	{
		path: "/events/:id",
		name: "event",
		component: () => import(/* webpackChunkName: "event" */ "../views/Event.vue"),
	},
	{
		path: "/invites",
		name: "invites",
		component: () => import(/* webpackChunkName: "invites" */ "../views/Invites.vue"),
	},
];

const router = new VueRouter({
	mode: "history",
	base: process.env.BASE_URL,
	routes,
});

export default router;
